<template>
  <div class="series-course">
    <el-form
      ref="ruleForm"
      :model="ruleForm"
      :rules="rules"
      label-width="80px"
      class="demo-ruleForm"
    >
      <el-form-item label="课程名称" prop="name">
        <el-input
          v-model="ruleForm.name"
          maxlength="20"
          show-word-limit
          placeholder="请输入课程名称"
          :disabled="disabled"
        />
      </el-form-item>
      <el-form-item label="重点内容" prop="content">
        <div
          v-if="!contentShow"
          class="div-textarea"
          :style="{ color: ruleForm.content ? '#515a6e' : '' }"
          @click="contentClick"
          v-html="ruleForm.content || '请输入重点内容'"
        />
        <vue-editor
          v-if="contentShow"
          ref="editor"
          v-model="ruleForm.content"
          placeholder="请输入重点内容"
          :use-custom-image-handler="true"
          @image-added="handleImageAdded"
        />
      </el-form-item>
      <el-form-item label="课程封面" prop="cover">
        <el-upload
          class="avatar-uploader"
          action=""
          :show-file-list="false"
          :on-success="handleAvatarSuccess"
          :before-upload="beforeAvatarUpload"
          :http-request="uploadFunc"
        >
          <img v-if="imgUrl" :src="imgUrl" class="avatar" />
          <i v-else class="el-icon-plus avatar-uploader-icon" />
          <div slot="tip" class="el-upload__tip">
            建议尺寸600*280px，JPG、PNG格式，图片小于5M
          </div>
        </el-upload>
      </el-form-item>
      <el-form-item
        v-if="ruleForm.column !== 'catalog'"
        label="课程栏目"
        prop="column"
      >
        <el-select v-model="ruleForm.column" placeholder="请选择">
          <el-option label="企业文化" value="corporate_culture" />
          <el-option label="健康管理师职责" value="service_responsibilities" />
          <el-option label="工作流程" value="workflow" />
          <el-option label="专业能力知识" value="professional_knowledge" />
        </el-select>
      </el-form-item>
      <el-form-item label="课程类型" prop="type">
        <el-radio-group v-model="ruleForm.type" @change="selectType">
          <el-radio label="image_text">图文</el-radio>
          <el-radio label="audio">音频</el-radio>
          <el-radio label="video">视频</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="课程详情" prop="detail">
        <vue-editor
          v-model="ruleForm.detail"
          placeholder="请输入课程详情"
          :use-custom-image-handler="true"
          @image-added="handleImageAdded"
        />
      </el-form-item>
      <el-form-item
        v-if="TipText"
        :label="ruleForm.type === 'audio' ? '上传音频' : '上传视频'"
      >
        <el-upload
          ref="upload"
          class="upload-demo"
          action=""
          :on-success="handleFileSuccess"
          :before-upload="beforeFileUpload"
          :http-request="uploadFileFunc"
          :file-list="fileList"
          :on-change="handleChange"
        >
          <el-button
            slot="trigger"
            size="small"
            type="primary"
            :disabled="isUpload"
            >上传{{ ruleForm.type === 'audio' ? '音频' : '视频' }}</el-button
          >
          <div slot="tip" class="el-upload__tip">{{ TipText }}</div>
        </el-upload>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')"
          >确定</el-button
        >
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { createSeries, detailCourse, editCourse } from '@/api/learningAdmin'
import { VueEditor } from 'vue2-editor'
import { upload } from '@/api/upload'
export default {
  components: {
    VueEditor
  },
  data() {
    return {
      ruleForm: {
        name: '',
        num: '',
        summary: '',
        content: '',
        column: '',
        cover: '',
        detail: '',
        url: ''
      },
      rules: {
        name: [
          {
            required: true,
            message: '您输入的内容有误，请重新输入',
            trigger: 'blur'
          }
        ],
        content: [
          {
            required: true,
            message: '您输入的内容有误，请重新输入',
            trigger: 'blur'
          }
        ],
        column: [
          { required: true, message: '请进行选择后再操作', trigger: 'change' }
        ],
        type: [
          { required: true, message: '请进行选择后再操作', trigger: 'change' }
        ],
        detail: [
          { required: true, message: '课程详情不能为空', trigger: 'blur' }
        ],
        cover: [
          { required: true, message: '请上传封面文件', trigger: 'change' }
        ]
      },
      imgUrl: '',
      disabled: false,
      uploading: false,
      uploadFile: false,
      contentShow: false,
      TipText: '', // 上传文件提示语
      routeName: '',
      parent_id: '',
      fileList: [],
      isUpload: false
    }
  },
  async created() {
    if (this.$route.name === 'singleEdit') {
      const { data, code } = await detailCourse(this.$route.params.id)
      if (code === 20000 && data.result) {
        this.ruleForm = {
          name: data.mtsCatalog.title
        }
        this.disabled = data.mtsCatalog.lesson_column === 'catalog'
        if (data.mtsCatalog.total_chapter) {
          this.ruleForm.total_chapter = data.mtsCatalog.total_chapter
        }
        if (data.mtsCatalog.summary) {
          this.ruleForm.summary = data.mtsCatalog.summary
        }
        if (data.mtsCatalog.content) {
          this.ruleForm.content = data.mtsCatalog.content
        }
        if (data.mtsCatalog.type) {
          this.ruleForm.type = data.mtsCatalog.type
          this.selectType()
        }
        if (data.mtsCatalog.lesson_column) {
          this.ruleForm.column = data.mtsCatalog.lesson_column
        }
        if (data.mtsCatalog.level) this.ruleForm.level = data.mtsCatalog.level
        if (data.mtsCatalog.cover) {
          this.imgUrl = data.mtsCatalog.cover
          this.ruleForm.cover = data.mtsCatalog.cover
        }
        this.parent_id = data.mtsCatalog.parent_id
        if (data.mtsCatalog.url) this.ruleForm.url = data.mtsCatalog.url
        if (data.mtsCatalog.detail) {
          this.ruleForm.detail = data.mtsCatalog.detail
        }
      }
    }
  },
  methods: {
    async uploadFunc(option) {
      const data = await upload(option.file, progress => {})
      this.imgUrl = data.url
      this.ruleForm.cover = data.url
    },
    async uploadFileFunc(option) {
      if (this.fileList.length > 0) return
      const data = await upload(option.file, progress => {})
      this.ruleForm.url = data.url
    },
    submitForm(formName) {
      if (this.uploading || this.uploadFile) {
        this.$message({ message: '正在上传文件中不能进行提交', type: 'error' })
        return false
      }
      this.$refs[formName].validate(async valid => {
        if (valid) {
          if (this.$route.params.id && this.$route.name === 'singleEdit') {
            const body = {
              title: this.ruleForm.name,
              content: this.ruleForm.content,
              type: this.ruleForm.type,
              detail: this.ruleForm.detail,
              cover: this.ruleForm.cover
            }
            if (this.ruleForm.url) {
              body.url = this.ruleForm.url
            }
            const column = this.ruleForm.column
            if (column && column !== 'catalog' && this.ruleForm.level === 1) {
              body.lesson_column = column
            }
            const { data, code } = await editCourse(this.$route.params.id, body)
            if (code === 20000) {
              if (data.result) {
                this.$message({ message: '修改成功', type: 'success' })
              } else this.$message({ message: data.message, type: 'error' })
              if (this.routeName === 'seriesDetail') {
                this.$router.push(
                  '/learning-admin/course/series-detail/' + this.parent_id
                )
              } else this.$router.push('/learning-admin/course')
            }
          } else {
            const body = {
              title: this.ruleForm.name,
              content: this.ruleForm.content,
              cover: this.ruleForm.cover,
              category: 'single_lesson',
              type: this.ruleForm.type,
              detail: this.ruleForm.detail
            }
            if (this.$route.params.id) {
              body.parent_id = this.$route.params.id
            }
            if (this.ruleForm.url) {
              body.url = this.ruleForm.url
            }
            const column = this.ruleForm.column
            if (column && column !== 'catalog') {
              body.lesson_column = column
            }
            const { data, code } = await createSeries(body)
            if (code === 20000) {
              if (data.result) {
                this.$message({ message: '添加成功', type: 'success' })
              } else this.$message({ message: data.message, type: 'error' })
              if (this.routeName === 'seriesDetail') {
                this.$router.push(
                  '/learning-admin/course/series-detail/' +
                    this.$route.params.id
                )
              } else this.$router.push('/learning-admin/course')
            }
          }
          // else end
        }
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
    },
    handleAvatarSuccess(res, file) {
      this.uploading = false
      this.$message({ message: '上传成功', type: 'success' })
    },
    handleFileSuccess() {
      this.uploadFile = false
      this.$message({ message: '上传成功', type: 'success' })
      this.isUpload = true
    },
    beforeFileUpload(file) {
      this.uploadFile = true
      let isSuccessType
      if (this.ruleForm.type === 'video') {
        isSuccessType =
          [
            'video/mp4',
            'video/avi',
            'video/flv',
            'video/wmv',
            'video/mkv',
            'video/mov'
          ].indexOf(file.type) >= 0
      }
      if (this.ruleForm.type === 'audio') {
        isSuccessType = file.type === 'audio/mp3'
      }
      if (!isSuccessType && this.ruleForm.type === 'video') {
        this.$message.error('支持avi、flv、mp4、wmv、mkv、mov、等视频格式')
      }
      if (!isSuccessType && this.ruleForm.type === 'audio') {
        this.$message.error('支持mp3格式')
      }
      if (!isSuccessType) this.fileList = []
      if (this.fileList.length > 1) {
        isSuccessType = false
        // this.fileList = this.fileList.slice(0, 1)
      }
      return isSuccessType
    },
    beforeAvatarUpload(file) {
      this.uploading = true
      const isJPG =
        ['image/png', 'image/jpeg', 'image/jpg'].indexOf(file.type) >= 0
      const isLt2M = file.size / 1024 / 1024 < 2

      if (!isJPG) {
        this.$message.error('上传头像图片只能是 JPG 格式!')
      }
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 5MB!')
      }
      return isJPG && isLt2M
    },
    selectType() {
      if (this.ruleForm.type === 'image_text') {
        this.TipText = ''
        this.rules.detail = [
          { required: true, message: '课程详情不能为空', trigger: 'blur' }
        ]
      }
      if (this.ruleForm.type === 'audio') {
        this.TipText = '可以上传100M以内的音频文件，支持mp3格式'
        this.rules.detail = []
      }
      if (this.ruleForm.type === 'video') {
        this.TipText =
          '可以上传3G以内的视频文件，支持avi、flv、mp4、wmv、mkv、mov、等视频格式上传'
        this.rules.detail = []
      }
    },
    handleChange(file, fileList) {
      this.fileList = fileList.slice()
    },
    contentClick() {
      this.contentShow = true
      const self = this
      setTimeout(function() {
        if (self.$refs.editor) {
          self.$refs.editor.quill.enable(true)
          self.$refs.editor.quill.focus()
        }
      }, 200)
    },
    async handleImageAdded(file, Editor, cursorLocation, resetUploader) {
      const data = await upload(file)
      const url = data.url // Get url from response
      Editor.insertEmbed(cursorLocation, 'image', url)
      resetUploader()
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      if (to.params.id && to.name !== 'singleEdit') {
        vm.routeName = 'seriesDetail'
      }
    })
  }
}
</script>

<style scoped lang="scss" type="text/scss">
.series-course {
  padding: 30px 20px;
  border: 1px solid #d7dae2;
  border-radius: 2px;
  /deep/.el-input {
    width: auto;
    .el-input__inner {
      width: 450px;
    }
  }
  /deep/.el-textarea__inner {
    height: 100px;
  }
  /deep/.avatar-uploader .el-upload {
    position: relative;
    overflow: hidden;
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
  }
  /deep/.avatar-uploader .el-upload:hover {
    border-color: #409eff;
  }
  /deep/.el-upload__tip {
    line-height: 12px;
  }
  /deep/.ql-header.ql-picker {
    line-height: normal;
  }
}
.avatar-uploader-icon {
  width: 600px;
  height: 280px;
  line-height: 280px;
  font-size: 28px;
  text-align: center;
  color: #8c939d;
}
.avatar {
  display: block;
  width: 600px;
  height: 280px;
}
.div-textarea {
  overflow-y: auto;
  padding: 5px 15px;
  width: 100%;
  height: 100px;
  color: #ccc;
  border: 1px solid #dcdfe6;
  border-radius: 4px;
  box-sizing: border-box;
  cursor: pointer;
  &.disabled {
    color: #c0c4cc;
    background-color: #f5f7fa;
    border-color: #e4e7ed;
    cursor: not-allowed;
  }
}
/deep/.el-form-item__content {
  line-height: 20px;
}
</style>
